<template  >
  <div class="mt-5">
    <b-table striped hover sticky-header :items="items" :fields="fields">
    <template  v-slot:cell(actions)="data">

        <b-button  :disabled="data.item.row" v-if="data.item.status== 'Managed'" @click="onClickManage(data,data.item.ip)" pill class="mx-1">Managed</b-button>
        <b-button :disabled="data.item.row" v-if="data.item.status== 'Unmanaged'" @click="onClickUnManage(data,data.item.ip)" pill class="mx-1">Unmanaged</b-button>
        <b-button @click="SNMP(data,data.item.ip)" pill class="mx-1">SNMP</b-button>
        <b-button pill class="mx-1">Delete</b-button>

      </template>
    </b-table>
  </div>
</template>

<script>


import Vue from 'vue'
import axios from 'axios';
import VueAxios from 'vue-axios'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
Vue.use(VueAxios,axios)


// Import Bootstrap and BootstrapVue CSS files (order is important)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Make BootstrapVue available throughout your project
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)

  export default {
    data() {
      return {
        items: undefined,
        fields: ['name', 'ip', 'status','actions'],
        timer: ''


      }

    },
   created () {
        this.fetchSeed();
        this.timer = setInterval(this.fetchSeed, 10000);
    },
   mounted()
  {
     this.fetchSeed();

  },
          methods: {
                  fetchSeed () {
      Vue.axios.get('/getDevices')
    .then((res)=>{
    this.items=res.data.data;
    console.warn(res.data.data)
    })
        },
            onClickManage(data,ip){
                     data.item.row=true;
                   Vue.axios.get('/manage/'+'?ip='+ip)
                .then((res)=>{

                if (res.data.status == "success")
                {
                 //  alert(data.item.actions)
                //   alert(res.data.data.ip)
                   data.item.status="Unmanaged"
                     data.item.row=false;

                }
                else
                {
                    alert("Unmanage failed")
                    data.item.row=false;

                }
                 })
            },
             onClickUnManage(data,ip){
                     data.item.row=true;
                   Vue.axios.get('/unmanage/'+'?ip='+ip)
                .then((res)=>{

                if (res.data.status == "success")
                {
                  // alert(data.item.actions)
                //   alert(res.data.data.ip)
                   data.item.status="Managed"
                     data.item.row=false;

                }
                else
                {
                    alert("Unmanage failed")
                    data.item.row=false;

                }
                 })
            },
                     SNMP(data,ip){
                     data.item.row=true;
                   Vue.axios.get('/unmanage/'+'?ip='+ip)
                .then((res)=>{

                if (res.data.status == "success")
                {
                  // alert(data.item.actions)
                //   alert(res.data.data.ip)
                   data.item.status="Managed"
                     data.item.row=false;

                }
                else
                {
                    alert("Unmanage failed")
                    data.item.row=false;

                }
                 })
            }

        }
  }
</script>